import { Box, Button, Card, Grid, styled, TextField } from '@mui/material';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import "./adminAuth.css";

const FlexBox = styled(Box)(() => ({
  display: 'flex',
  alignItems: 'center',
}));

const JustifyBox = styled(FlexBox)(() => ({
  justifyContent: 'center',
}));

const ContentBox = styled(Box)(({ theme }) => ({
  padding: 32,
  background: theme.palette.background.default,
}));

const ForgotPasswordRoot = styled(JustifyBox)(() => ({
  background: '#e7feff',
  minHeight: '100vh !important',
  '& .card': {
    maxWidth: 900,
    minHeight: 500,
    margin: '1rem',
    borderRadius: 2,
  },
}));

const ForgotPassword = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState('admin@example.com');

  const handleFormSubmit = () => {
    console.log(email);
  };

  return (
    <ForgotPasswordRoot>
      <Card className="card">
        <Grid container>
          <Grid item lg={7} sm={6} xs={12}>
            <div className='login-left'>
              <JustifyBox p={4} height="100%" sx={{ minWidth: 320 }}>
                <img src={`${process.env.PUBLIC_URL}/assets/images/loginImg.png`} alt="" title='Login Image' />
              </JustifyBox>
            </div>

          </Grid>

          <Grid item lg={5} sm={6} xs={12}>
            <div className='login-right'>
              <ContentBox>
                <h2>Reset Password</h2>
                <p>Enter your registered email below to receive reset instructions</p>
                <br />
                <form onSubmit={handleFormSubmit}>
                  <label htmlFor="email" className='mobile-label'>Enter your email</label>

                  <TextField
                    type="email"
                    name="email"
                    size="small"
                    // label="Email"
                    value={email}
                    variant="outlined"
                    onChange={(e) => setEmail(e.target.value)}
                    sx={{ mb: 3, width: '100%' }}
                  />

                  <Button fullWidth variant="contained" color="primary" type="submit">
                    Reset Password
                  </Button>

                  <Button
                    fullWidth
                    color="primary"
                    variant="outlined"
                    onClick={() => navigate(-1)}
                    sx={{ mt: 2 }}
                  >
                    Go Back
                  </Button>
                </form>
              </ContentBox>
            </div>
          </Grid>
        </Grid>
      </Card>
    </ForgotPasswordRoot>
  );
};

export default ForgotPassword;
